<template>
	<DefaultLayout>
		<section slot="content" >
			<b-container class="bv-example-row">
				<b-row align-h="center">
					<b-col 
						cols="5" sm="5" md="5" lg="3" xl="2"
						class="m-1 p-0 boxArea d-flex align-items-center justify-content-center"
						v-for="(opc,index) in arrTemas"
						:key="index"
						@click.prevent="goToQuestions(opc)">
					<Temario :tema="opc"></Temario>
						</b-col>
				</b-row>
			</b-container>
		</section>
		<section slot="pie" >
			<p class="text-right m-0">Prueba cuánto sabes de ...</p>
		</section>
	</DefaultLayout>
</template>
<script>
import DefaultLayout from '@/layouts/DefaultLayout' // eslint-disable-line no-unused-vars
import Temario from '@/components/Temario.vue'
import {db} from '../main';

export default {
	name: 'TemasView',
	components: {
		DefaultLayout,
		Temario
	},
	data() {
		//var vm = this;
		return {
			arrTemas: [],
			chosenArea: this.$route.params.areaSelect
		}
	},
	created() {
		this.getTemas();
	},
	methods: {
		async getTemas() {
			try {
				const snapshot = await db.collection('menu').where('area','==', this.chosenArea).get();
				const events = [];
				
				snapshot.forEach(doc => {
					events.push({ id: doc.id, ...doc.data() });
					
				})
				this.arrTemas = events[0].temas;
			} catch (error) {
				console.log(error);
			}
		},
		goToQuestions(opc) {
        this.boxTwo = ''
        this.$bvModal.msgBoxConfirm('Recuerda que tienes 10seg por cada pregunta ...', {
          title: 'Estás list@ para responder?',
          size: 'sm',
          buttonSize: 'sm',
          okVariant: 'danger',
          okTitle: 'SI',
          cancelTitle: 'NO',
          footerClass: 'p-2',
          hideHeaderClose: false,
          centered: true
        })
          .then(value => {
            if(value === true) {
              this.$router.push({
								params: {
									areaSelect: this.chosenArea,
									temaSelect: opc.tema
									
								},
								name: 'Questions',
							})
            }
          })
          .catch(err => {
            // An error occurred
          })
    }
	}
}
</script>
<style>
@import '../assets/styles/global.css';
</style>