<template>
	<DefaultLayout>
		<section slot="head" >
			<b-button v-b-toggle href="#example-sidebar" @click.prevent class="btn-instrucciones">Instrucciones</b-button>
		</section>
		<section slot="content" >
			<b-container class="bv-example-row">
				<b-row align-h="center">
					<b-col 
						cols="5" sm="5" md="5" lg="3" xl="2"
						class="m-1 p-0 boxArea d-flex align-items-center justify-content-center"
						v-for="(event,index) in events"
						:key="index"
						@click.prevent="goToTemas(event)">
							<Areas :areas="event"></Areas>
						</b-col>
				</b-row>
			</b-container>
			<b-sidebar id="example-sidebar" title="Instrucciones Quizplay" shadow >
				<div class="px-3 py-2">
					<b-list-group>
						<b-list-group-item href="#" class="flex-column align-items-start">
							<div class="d-flex w-100 justify-content-between">
								<h5 class="mb-1" style="color:var(--celeste-oscuro)">Areas</h5>
								<small class="text-white number-list">1</small>
							</div>					
							<p class="mb-1 text-left" >
								Elige una de las áreas que te mostramos
							</p>
						</b-list-group-item>					
						<b-list-group-item href="#" class="flex-column align-items-start">
							<div class="d-flex w-100 justify-content-between">
								<h5 class="mb-1" style="color:var(--celeste-oscuro)">Temas</h5>
								<small class="text-white number-list">2</small>
							</div>					
							<p class="mb-1 text-left">
								Dale click a tu tema preferido
							</p>
						</b-list-group-item>					
						<b-list-group-item href="#" class="flex-column align-items-start">
							<div class="d-flex w-100 justify-content-between">
								<h5 class="mb-1" style="color:var(--celeste-oscuro)">Cuestionario</h5>
								<small class="text-white number-list">3</small>
							</div>					
							<p class="mb-1 text-left">
								3.1.- En la parte superior verás correr el tiempo desde 0 a 10seg,
								si no respondes dentro del tiempo se calificará la respuesta como incorrecta.
							</p>
							<p class="mb-1 text-left">
								3.2.- En la parte inferior podrás identificar la calificación de tus respuestas,
								se pintará de verde las correctas y rojo las fallidas.
							</p>
							<div class="d-flex justify-content-center">
								<div class="prog" style="background-color:red"></div>
								<div class="prog" style="background-color:red"></div>
								<div class="prog" style="background-color:green"></div>
								<div class="prog" style="background-color:red"></div>
								<div class="prog" style="background-color:green"></div>
							</div>
							<p class="mb-1 text-left">
								3.3.- Al terminar el quiz te damos la opción de ver las respuestas.
							</p>
						</b-list-group-item>
					</b-list-group>
				</div>
			</b-sidebar>
		</section>
		<section slot="pie" >
			<p class="text-right m-0">Prueba cuánto sabes de ...</p>
		</section>
	</DefaultLayout>
</template>

<script>
import DefaultLayout from '@/layouts/DefaultLayout'
import Areas from '@/components/Areas.vue'
import {db} from '../main';

export default {
	name: 'HomeView',
	components: {
		Areas,
		DefaultLayout
	},
	data() {
		return {
			events:[]
		}
	},
	created() {
		this.getEvents();
	},
	methods: {
		async getEvents() {
			try {
				const snapshot = await db.collection('menu').get();
				const events = [];
				
				snapshot.forEach(doc => {
					events.push({ id: doc.id, ...doc.data() });
				})
				this.events = events;
			} catch (error) {
				console.log(error);
			}
		},
		goToTemas(event) {
			this.$router.push({
				params: {
					areaSelect:event.area,
					event
				},
				name: 'Temas',
			})
		}
	}
}
</script>
<style>
	@import '../assets/styles/global.css';
