<template>
	<DefaultLayout>
		<section slot="head" >
			<div class="d-flex flex-column align-items-center" style="width:120px">	
				<p id="counter" class="counter oculto text-white mb-0 h2 align-self-end" v-html="counter"></p>
				<b-progress
					:value="ancho"
					:max="10"
					show-value
					height="20px"
					:variant="color"
					style="font-size:1rem"
					class="w-100 align-self-center none"></b-progress>
			</div>
		</section>
		<section slot="content" >
			<b-card no-body class="overflow-hidden border-none" >
				<b-row no-gutters class="align-items-center">
					<b-col md="6" class="visible">
						<b-img-lazy :src="imgQuiz" alt="Image" class="rounded-0" fluid-grow></b-img-lazy>
					</b-col>				
					<b-col md="6" class="d-flex px-4">
						<b-card-body class="p-0">
							<b-card-text id="question" v-html="question">
							</b-card-text>
							<b-list-group>
								<b-list-group-item variant="dark" id="A" @click="checkAnswer('A')" v-html="choiceA"></b-list-group-item>
								<b-list-group-item variant="dark" id="B" @click="checkAnswer('B')" v-html="choiceB"></b-list-group-item>
								<b-list-group-item variant="dark" id="C" @click="checkAnswer('C')" v-html="choiceC"></b-list-group-item>
								<b-list-group-item variant="dark" id="C" @click="checkAnswer('D')" v-html="choiceD"></b-list-group-item>							
							</b-list-group>
						</b-card-body>
					</b-col>
				</b-row>
			</b-card>
		</section>
		<section slot="pie" class="d-flex justify-content-between align-items-center">
			<p class="text-white mb-0 visible">Pregunta {{runningQuestionIndex+1}} de {{lastQuestionIndex+1}}</p>
			<p class="text-white mb-0 oculto">{{runningQuestionIndex+1}} / {{lastQuestionIndex+1}}</p>
			<div id="progress" class="d-flex justify-content-end progreso" v-html="progress"></div>
		</section>
	</DefaultLayout>
</template>
<script>
import DefaultLayout from '@/layouts/DefaultLayout' // eslint-disable-line no-unused-vars
import Questionary from '@/components/Questionary.vue'
import {db} from '../main'
import Logo from '@/assets/images/logo.png'
import Premio from '@/assets/images/premio.png'
import Aplausos from '@/assets/images/aplausos.png'
import Okay from '@/assets/images/okay.png'
import Insatisfaccion from '@/assets/images/insatisfaccion.png'

export default {
	name: 'QuestionsView',
	components: {
		DefaultLayout,
		Questionary
	},
	data() {
		//var vm = this;
		return {
			Logo: Logo,
			Premio:Premio,
			Aplausos: Aplausos,
			Okay: Okay,
			Insatisfaccion: Insatisfaccion,
			arrQuestions: [],
			chosenArea: this.$route.params.areaSelect,
			chosenTema: this.$route.params.temaSelect,
			lastQuestionIndex: 0,
			runningQuestionIndex:0,
			q: [],
			qIndex: 0,
			questionTime: 10,
			gaugeWidth: 150,
			count: 1,
			gaugeProgressUnit: this.gaugeWidth/this.questionTime,
			score: 0,
			TIMER: '',
			
			question: '',
			imgQuiz: '',
			choiceA:'',
			choiceB:'',
			choiceC:'',
			choiceD: '',
			counter: 0,
			btimeGauge:'',
			timeGauge:'',
			progress:'',
			//animate: true
		}
	},
	created() {
		this.getQuestions();
	},
	mounted() {
		this.TIMER = setInterval(this.counterRender,1000);
	},
	computed: {
		ancho() {
			return (this.count-1)
		},
		color() {
			let variant = (this.count <=4)? 'info' :
										(this.count >4 && this.count <=7) ? 'warning' :'danger';
			return variant
		},
		image() {
			let scorePerCent = Math.round(100*this.score/this.arrQuestions.length)
			let img = (scorePerCent === 100)? Premio:
								(scorePerCent >= 80 && scorePerCent < 100)? Aplausos:
								(scorePerCent >=60 && scorePerCent <80)? Okay:
								Insatisfaccion;
			return img
		}
	
	},
	watch: {
		arrQuestions: {
			inmediate: true,
			handler: function () {
				if(this.arrQuestions) {
					this.q = this.arrQuestions[this.runningQuestionIndex];
					this.question = this.q.question;
					this.choiceA = this.q.choiceA;
					this.choiceB = this.q.choiceB;
					this.choiceC = this.q.choiceC;
					this.choiceD = this.q.choiceD;
					this.imgQuiz = this.q.img
				} else {
					console.log('no hay valor')
				}
			}
		},
		lastQuestionIndex: {
			inmediate: true,
			handler: function () {
				if(this.lastQuestionIndex) {
					for( this.qIndex; this.qIndex <= this.lastQuestionIndex; this.qIndex++) {
					this.progress += `<div class="prog" id="${this.qIndex}"></div>`;
			}
				}
			}
		}
		
	},
	methods: {
		async getQuestions() {
			try {
				const snapshot = await db.collection('menu').where('area','==', this.chosenArea).get();
				const events = [];
				
				snapshot.forEach(doc => {
					events.push({ id: doc.id, ...doc.data() });
				})
				this.arrQuestions = events[0].temas.filter(obj => 
				obj.tema === this.chosenTema)[0].questions;
				this.lastQuestionIndex= this.arrQuestions.length-1;
			} catch (error) {
				console.log(error);
			}
		},
		questionRender() {
			this.q = this.arrQuestions[this.runningQuestionIndex];
			this.question = this.q.question;
			this.choiceA = this.q.choiceA;
			this.choiceB = this.q.choiceB;
			this.choiceC = this.q.choiceC;
			this.choiceD = this.q.choiceD;
			this.imgQuiz = this.q.img;
		},
		answerIsCorrect() {
			document.getElementById(`${this.runningQuestionIndex}`).classList.add('colorGreen');
		},
		answerIsWrong() {
			document.getElementById(`${this.runningQuestionIndex}`).classList.add('colorRed');
			
		},
		counterRender() {
			if(this.count <= this.questionTime) {
				this.counter = this.count;
				this.count++;
			} else {
				this.count = 1;
				this.counter = 0;
				this.answerIsWrong();
				if(this.runningQuestionIndex < this.lastQuestionIndex) {
					this.runningQuestionIndex++;
					
					this.questionRender();
				} else {
					clearInterval(this.TIMER);
					this.scoreRender();
				}
			}
		},
		checkAnswer(answer) {
			if(this.arrQuestions[this.runningQuestionIndex].correct == answer) {
				this.score++;
				this.answerIsCorrect();
			} else {
				this.answerIsWrong();
			}
			if(this.runningQuestionIndex < this.lastQuestionIndex) {
				this.count = 1;
				this.counter = 0;
				this.runningQuestionIndex++;
				this.questionRender();
			} else {
				clearInterval(this.TIMER);
				this.scoreRender();
			}
		},
		scoreRender() {
				const h = this.$createElement
				const messageVNode = h('div', { class: ['foobar'] }, [
					//h('p', { class: ['text-center'] }, [
					//	' Flashy ',
					//	h('strong', 'msgBoxOk'),
					//	' message ',
					//]),
					
					h('b-img', {
						props: {
							src: this.image,
							thumbnail: true,
							center: true,
							fluid: true
						}
					})
				])
				this.$bvModal.msgBoxConfirm([messageVNode], {
					title: `${this.score} de ${this.lastQuestionIndex+1}`,
					size: 'sm',
					buttonSize: 'sm',
					okVariant: 'success',
					headerClass: 'p-2 border-bottom-0',
					footerClass: 'p-2 border-top-0',
					hideHeaderClose: false,
					centered: true
				})
					.then(value => {
						this.$router.push({ name:'Home'})
					})
					.catch(err => {
						// An error occurred
					})
		}
	}
}
</script>
<style>
	@import '../assets/styles/global.css';
</style>